import { updateUser, getUser } from "@/api/auth";
const Cookie = process.client ? require("js-cookie") : undefined;
export default {
  name: "Settings",
  middleware: ["auth"],
  data() {
    return {
      user: {
        email: "",
        bio: "",
        image: "",
        username: "",
        password: ""
      }
    };
  },
  async mounted() {
    const res = await getUser();
    this.user = res.user;
  },
  methods: {
    async handleSubmit() {
      try {
        const res = await updateUser(this.user);
        this.$router.push("/");
      } catch (err) {
        alert("更新失败");
        // console.log(err);
      }
    },
    logout() {
      Cookie.remove("user");
      this.$store.commit("setUser", null);
      this.$router.redirect("/");
    }
  }
};
